"use client";
import React, { useState } from "react";
import { useRouter, useParams } from "next/navigation";
import api from "@/services/backend-api/authApi"

const RegisterForm: React.FC = () => {
  const router = useRouter();
  const { token } = useParams();
  const [formData, setFormData] = useState({
    email: "",
    username: "",
    first_name: "",
    last_name: "",
    password: "",
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    // Здесь вы можете добавить логику для отправки данных на сервер
    try {
      const data = await api.signup(token as string, formData);
      // Пример сохранения токена в localStorage после успешной авторизации
      router.push("/api/sign_in");
      console.log(data);
    } catch (error) {
      setError(error as string);
      console.error(error);
    }
    console.log("Регистрационные данные:", formData);
  };

  return (
    <div className="text-mainText max-w-md mx-auto mt-10">
      <form
        onSubmit={handleSubmit}
        className="bg-background rounded px-8 pt-6 pb-8 mb-4"
      >
        <div className="mb-4">
          <label className="block text-sm font-bold mb-2" htmlFor="email">
            Email
          </label>
          <input
            className="appearance-none border-b-2 border-b-mainText bg-background w-full py-2 px-3  leading-tight focus:outline-none focus:shadow-outline"
            id="email"
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
          />
        </div>
        <div className="mb-4">
          <label className="block  text-sm font-bold mb-2" htmlFor="username">
            Username
          </label>
          <input
            className="appearance-none border-b-2 border-b-mainText bg-background w-full py-2 px-3  leading-tight focus:outline-none focus:shadow-outline"
            id="username"
            type="text"
            name="username"
            value={formData.username}
            onChange={handleChange}
            required
          />
        </div>
        <div className="mb-4">
          <label className="block  text-sm font-bold mb-2" htmlFor="first_name">
            Имя
          </label>
          <input
            className="appearance-none border-b-2 border-b-mainText bg-background w-full py-2 px-3  leading-tight focus:outline-none focus:shadow-outline"
            id="first_name"
            type="text"
            name="first_name"
            value={formData.first_name}
            onChange={handleChange}
            required
          />
        </div>
        <div className="mb-4">
          <label className="block  text-sm font-bold mb-2" htmlFor="last_name">
            Фамилия
          </label>
          <input
            className="appearance-none border-b-2 border-b-mainText bg-background w-full py-2 px-3  leading-tight focus:outline-none focus:shadow-outline"
            id="last_name"
            type="text"
            name="last_name"
            value={formData.last_name}
            onChange={handleChange}
            required
          />
        </div>
        <div className="mb-6">
          <label className="block  text-sm font-bold mb-2" htmlFor="password">
            Пароль
          </label>
          <input
            className="appearance-none border-b-2 border-b-mainText bg-background w-full py-2 px-3  leading-tight focus:outline-none focus:shadow-outline"
            id="password"
            type="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
            required
          />
        </div>
        <div className="flex items-center justify-end">
          <button
            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
            type="submit"
          >
            Зарегистрироваться
          </button>
          {error && (
            <div className="text-red-500 text-xs italic mb-4">{error}</div>
          )}
        </div>
      </form>
    </div>
  );
};

export default RegisterForm;
